"use strict";
Object.defineProperty(exports, "__esModule", { value: true });
const _ = require("lodash");
const char_i_1 = require("../data/char.i");
const data_1 = require("../data");
const util_1 = require("../util");
const chalk_1 = require("chalk");
const uniqArray = (arr) => {
    const tmp = _.uniq(arr);
    if (tmp.length === 1)
        return tmp;
    return arr;
};
let collectedPic = new Set();
exports.toSkinFile = (head) => {
    const charName = head.match(/(char|token|trap)_(\d+)_([A-Za-z0-9]+)/);
    if (charName) {
        const skinTail = head.replace(`${charName[0]}_`, "").replace("#", "-");
        const skinHead = skinTail.split("-")[0];
        const skinHash = skinTail.split("-")[1];
        const skinid = skinTail.length === head.length ? head : charName[0] + (skinHash ? "@" + skinHead + "#" + skinHash : "#" + skinTail);
        const skin = data_1.skin_table.charSkins[skinid];
        if (skin) {
            const skinSeq = skinTail;
            const idMap = {
                "1+": 2,
                "2": 3,
            };
            const fileName = charName[3] + "-" + (idMap[skinSeq] || skinSeq);
            return util_1.firstCase(fileName);
        }
    }
    return;
};
// 转换数据到需要的形式
exports.translateCharacter = (char, handbook) => {
    let dst = {};
    // base
    dst.id = char.id;
    dst.name = char.name;
    dst.rarity = char.rarity;
    if (char.description)
        dst.description = char.description.replace(/\\n/g, "\n");
    if (!char.canUseGeneralPotentialItem)
        dst.canUseGeneralPotentialItem = char.canUseGeneralPotentialItem;
    if (char.potentialItemId)
        dst.potentialItemId = char.potentialItemId;
    dst.displayNumber = char.displayNumber;
    dst.appellation = char.appellation;
    dst.position = char.position;
    dst.tagList = char.tagList;
    dst.displayLogo = char.displayLogo;
    dst.itemUsage = char.itemUsage;
    dst.itemDesc = char.itemDesc;
    dst.itemObtainApproach = char.itemObtainApproach;
    dst.maxPotentialLevel = char.maxPotentialLevel;
    dst.tokenKey = char.tokenKey;
    if (char.team > -1) {
        dst.team = {
            key: data_1.handbook_team_table[char.team + ""].teamKey,
            name: data_1.handbook_team_table[char.team + ""].teamName,
            color: data_1.handbook_team_table[char.team + ""].color,
        };
    }
    // 职业
    dst.profession = char_i_1.Profession[char.profession]; // 转换成中文
    // 潜能
    if (char.potentialRanks)
        dst.potentialRanks = char.potentialRanks.map(v => v.description);
    // 归并数据
    if (char.phases && char.phases[0] && char.phases[0].maxLevel) {
        dst.maxLevel = [];
        dst.rangeId = [];
        dst.evolveCost = [];
        const prop4list = ["maxHp", "atk", "def"];
        const prop3list = [
            "magicResistance",
            "cost",
            "blockCnt",
            "attackSpeed",
            "baseAttackTime",
            "respawnTime",
            "hpRecoveryPerSec",
            "spRecoveryPerSec",
            "tauntLevel",
            "massLevel",
            "baseForceLevel",
            "stunImmune",
            "silenceImmune",
        ];
        char.phases.forEach((phase, index) => {
            // 初始数据
            if (index === 0) {
                prop4list.forEach(prop => {
                    dst[prop] = [phase.attributesKeyFrames[0].data[prop]];
                });
                prop3list.forEach(prop => {
                    dst[prop] = [];
                });
            }
            dst.maxLevel.push(phase.maxLevel);
            dst.rangeId.push(phase.rangeId);
            // 满级数据
            prop4list.concat(prop3list).forEach(prop => {
                dst[prop].push(phase.attributesKeyFrames[1].data[prop]);
            });
            if (phase.evolveCost) {
                dst.evolveCost.push(phase.evolveCost.map(v => {
                    const item = data_1.item_table.items[v.id];
                    return { name: item.name, count: v.count };
                }));
            }
        });
        dst.rangeId = uniqArray(dst.rangeId);
        // 将所有数值都一样的换成一个值
        prop3list.concat(prop4list).forEach(prop => {
            dst[prop] = uniqArray(dst[prop]);
        });
        if (util_1.isEmpty(dst.evolveCost))
            delete dst.evolveCost;
    }
    // 删除默认值
    {
        const checkDefault = (obj, key, def) => {
            const arr = obj[key];
            if (arr.length === 1 && arr[0] === def)
                delete obj[key];
        };
        checkDefault(dst, "hpRecoveryPerSec", 0);
        checkDefault(dst, "spRecoveryPerSec", 1);
        checkDefault(dst, "tauntLevel", 0);
        checkDefault(dst, "massLevel", 0);
        checkDefault(dst, "baseForceLevel", 0);
        checkDefault(dst, "stunImmune", false);
        checkDefault(dst, "silenceImmune", false);
    }
    // 技能
    if (char.skills) {
        dst.skills = [];
        char.skills.forEach(v => {
            if (!v.skillId)
                return;
            const skill = data_1.skill_table[v.skillId];
            if (skill) {
                const s = {
                    // id: skill.skillId,
                    name: skill.levels[0].name,
                    phase: v.unlockCond.phase,
                };
                if (v.unlockCond.level != 1)
                    s.level = v.unlockCond.level;
                // 专精材料
                const cost = v.levelUpCostCond.map(v => {
                    // if (!v.levelUpCost) console.log(char.name);
                    return (v.levelUpCost &&
                        v.levelUpCost.map(v => {
                            const item = data_1.item_table.items[v.id];
                            return { name: item.name, count: v.count };
                        }));
                });
                if (cost.length && cost[0] != null)
                    s.masterCost = cost;
                dst.skills.push(s);
            }
            else {
                console.log(chalk_1.default.red("[error]"), v.skillId, "skill not found");
            }
        });
        if (!dst.skills.length)
            delete dst.skills;
    }
    // 技能升级
    if (char.allSkillLvlup && char.allSkillLvlup.length) {
        dst.skillCost = char.allSkillLvlup.map(rankup => {
            if (!rankup.lvlUpCost) {
                return [];
            }
            return rankup.lvlUpCost.map(cost => {
                const item = data_1.item_table.items[cost.id];
                return {
                    name: item.name,
                    count: cost.count,
                };
            });
        });
    }
    // 天赋
    if (char.talents) {
        dst.talents = [];
        char.talents.forEach(talent => {
            if (!talent.candidates)
                return;
            const s = {
                // id: skill.skillId,
                name: talent.candidates[0].name,
                phase: talent.candidates[0].unlockCondition.phase,
                level: talent.candidates[0].unlockCondition.level,
                desc: talent.candidates[0].description,
                upgrades: talent.candidates.slice(1).map(v => {
                    const rst = {};
                    if (talent.candidates[0].name != v.name)
                        rst.name = v.name;
                    rst.phase = v.unlockCondition.phase;
                    if (!v.unlockCondition.level && v.unlockCondition.level != 1)
                        rst.level = v.unlockCondition.level;
                    if (v.requiredPotentialRank)
                        rst.potential = v.requiredPotentialRank;
                    rst.desc = v.description;
                    return rst;
                }),
            };
            if (talent.candidates[0].unlockCondition.level != 1)
                s.level = talent.candidates[0].unlockCondition.level;
            dst.talents.push(s);
        });
    }
    // handbook补充数据
    if (handbook) {
        dst.cv = handbook.infoName;
        dst.art = handbook.drawName;
    }
    // skin补充数据
    const skins = _.filter(data_1.skin_table.charSkins, v => v.charId === char.id);
    if (skins && skins.length > 0) {
        dst.skins = skins.map(v => {
            const skin = {
                // id: v.portraitId,
                name: v.displaySkin.skinGroupName,
                // desc: v.displaySkin.content,
                file: exports.toSkinFile(v.portraitId) + ".png",
            };
            collectedPic.add(v.portraitId);
            if (v.displaySkin.drawerName && v.displaySkin.drawerName.toLowerCase() !== dst.art.toLowerCase())
                skin.drawer = v.displaySkin.drawerName;
            return skin;
        });
    }
    // charwork 补充数据
    if (data_1.charword_table[char.id + "_CN_011"])
        dst.appearance = data_1.charword_table[char.id + "_CN_011"].voiceText;
    // extra补充数据 (基建)
    const extra = data_1.char_extra_table[dst.name];
    if (extra) {
        Object.assign(dst, extra);
    }
    return dst;
};
