"use strict";
Object.defineProperty(exports, "__esModule", { value: true });
const bot_1 = require("./wiki/bot");
const fs = require("fs-extra");
const var_1 = require("./var");
const chalk_1 = require("chalk");
const path = require("path");
const util_1 = require("./util");
require("dotenv").config();
const uploadImage = async (dir, bot, force = false, skip = "") => {
    const files = await fs.readdir(var_1.TARGET_PREFIX + dir + "/");
    const TH = 3;
    let skipv = skip, skipedFiles = 0;
    if (force)
        console.log(chalk_1.default.red(`force mode enabled`));
    if (skipv)
        console.log(chalk_1.default.green(`start skip to ${skip}`));
    for (let i = 0; i < files.length; i += TH) {
        const job = async (file) => {
            const name = path.basename(file);
            if (skipv) {
                ++skipedFiles;
                if (skipv === name) {
                    console.log(chalk_1.default.green(`skipped ${skipedFiles}/${files.length} files`));
                    skipv = "";
                }
                return;
            }
            let done = false;
            do {
                try {
                    if (force || !(await bot.getImageInfo(name))) {
                        const rst = await bot.uploadFile(file);
                        console.log("[sync]", name, rst.upload ? chalk_1.default.green("uploaded") + " " + rst.upload.result : chalk_1.default.red("error"));
                    }
                    done = true;
                }
                catch (e) {
                    console.log(chalk_1.default.red("[sync] upload failed => [auto retry]"), name);
                }
            } while (!done);
        };
        const jobfiles = files.slice(i, i + TH).map(v => var_1.TARGET_PREFIX + dir + "/" + v);
        await Promise.all(jobfiles.map(job));
    }
};
const syncPage = async (bot, title, text) => {
    if ((await bot.raw(title)) !== text) {
        console.log("[sync]", chalk_1.default.greenBright("diff detected:"), title);
        const rst = await bot.edit({ title, text });
        if (rst.error) {
            console.log(chalk_1.default.red("[syncPage]"), rst);
        }
    }
};
const syncPageFromFile = async (bot, rawTitle, localFile, base = var_1.TARGET_PREFIX) => {
    const file = base + localFile;
    const localRaw = await fs.readFile(file, "utf-8");
    return await syncPage(bot, rawTitle, localRaw);
};
const downloadPage = async (bot, rawTitle, localFile) => {
    const text = await bot.raw(rawTitle);
    if (text)
        await fs.writeFile(localFile, text);
};
const modulesList = "Enemy/Stage/Character/Item/Util/Charword".split("/");
const pullModules = async (bot) => {
    const pull = (module) => {
        return downloadPage(bot, `Module:${module}`, `src/module/${module}.lua`);
    };
    for (const mo of modulesList) {
        console.log("pull", mo);
        await pull(mo);
    }
};
const pushModules = async (bot) => {
    const push = (module) => {
        return syncPageFromFile(bot, `Module:${module}`, `src/module/${module}.lua`, "");
    };
    for (const mo of modulesList) {
        await push(mo);
    }
};
const uploadModuleData = async (bot) => {
    await Promise.all([
        syncPageFromFile(bot, "Module:Character/data", "CharacterData.lua"),
        syncPageFromFile(bot, "Module:Item/data", "ItemData.lua"),
        syncPageFromFile(bot, "Module:Stage/data", "StageData.lua"),
        syncPageFromFile(bot, "Module:Enemy/data", "EnemyData.lua"),
        syncPageFromFile(bot, "Module:Skill/data", "SkillData.lua"),
    ]);
};
const purgeWithTemplate = async (bot, tplName) => {
    const rst = await bot.purge({ generator: "transcludedin", titles: tplName, gtilimit: 500 });
    console.log(util_1.formatJSON(rst));
};
const syncMultiPages = async (bot, file, base = var_1.TARGET_PREFIX) => {
    const pages = JSON.parse(await fs.readFile(base + file, "utf-8"));
    for (let i = 0; i < pages.length; i++) {
        const page = pages[i];
        await syncPage(bot, page.title, page.text);
    }
};
const syncSinglePage = async (bot, title, file) => {
    const text = await fs.readFile(var_1.TARGET_PREFIX + file, "utf-8");
    await syncPage(bot, title, text);
};
exports.default = async (argv) => {
    const bot = new bot_1.WikiBot("https://arknights.huijiwiki.com/", process.env.user, process.env.session);
    await bot.getToken();
    const mode = argv.mode;
    const force = argv.force;
    // 自动上传图片
    if (mode === "char") {
        console.log("[sync] uploadImage(char) start");
        await uploadImage("char", bot, force);
    }
    if (mode === "itemimg") {
        console.log("[sync] uploadImage(item) start");
        await uploadImage("items", bot, force, argv.skip);
    }
    if (mode === "map") {
        console.log("[sync] uploadImage(map) start");
        await uploadImage("maps", bot, force);
    }
    if (mode === "cv") {
        await uploadImage("cv", bot, force);
    }
    if (mode === "skillimg") {
        console.log("[sync] uploadImage(skill) start");
        await uploadImage("skills", bot, force);
    }
    // 同步模块数据
    if (mode === "module") {
        // const raw = await bot.raw("Module:Character/data");
        console.log("[sync] uploadModuleData start");
        await uploadModuleData(bot);
    }
    // 同步
    if (mode === "book") {
        console.log("[sync] sync char.*sync.json start");
        await syncMultiPages(bot, "Char.sync.json");
        await syncMultiPages(bot, "CharSkill.sync.json");
        await syncMultiPages(bot, "CharHandbook.sync.json");
        await syncMultiPages(bot, "CharWord.sync.json");
    }
    if (mode === "enemy") {
        console.log("[sync] sync enemy.*sync.json start");
        await syncMultiPages(bot, "Enemy.sync.json");
    }
    if (mode === "stage") {
        console.log("[sync] sync stage.*sync.json start");
        await syncMultiPages(bot, "Stage.sync.json");
    }
    if (mode === "pull") {
        console.log("[sync] pull modules start");
        await pullModules(bot);
    }
    if (mode === "push") {
        console.log("[sync] push modules start");
        await pushModules(bot);
    }
    if (mode === "purge") {
        await purgeWithTemplate(bot, "template:NavboxEnemy");
        await purgeWithTemplate(bot, "template:NavboxChar");
        await purgeWithTemplate(bot, "template:NavboxStage");
    }
    if (mode === "tab") {
        await syncSinglePage(bot, "Data:Charword.tab", "CharWord.tab.json");
    }
    console.log("[sync] All Finished");
};
