const convertCharImage = async () => {
    const basedir = var_1.TMP_PREFIX + "DB/Texture2D/";
    const outdir = var_1.TARGET_PREFIX + "char/";
    await fs.ensureDir(outdir);
    const files = await fs.readdir(basedir);
    // const hasPathid = /#\d\d+/;
    let mainFiles = [];
    for (let i = 0; i < files.length; i++) {
        const name = files[i];
        if (name.includes("[alpha]")) {
            const size = await util_1.imgSizeOf(basedir + name);
            if (size.width >= 1024 && size.height >= 1024) {
                const head = name.substr(0, name.indexOf("[alpha]"));
                let outName = char_1.toSkinFile(head);
                if (!outName)
                    console.log(chalk_1.default.red("unknown skin"), head);
                const re = new RegExp(`^${head.replace("+", "\\+")}(?: #\\d+)?\\.png$`);
                const origin = await Promise.all(files.filter(v => re.test(v)).map(async (name) => ({ name, ...(await util_1.imgSizeOf(basedir + name)) })));
                const main = origin.find(v => v.height >= 1024);
                if (!main) {
                    console.log(chalk_1.default.red("cant find file", name));
                }
                else {
                    if (outName)
                        mainFiles.push([main.name, name, outName + ".png"]);
                }
            }
        }
    }
    // 并行进程数
    const STEP = 2;
    for (let i = 0; i < mainFiles.length; i += STEP) {
        const group = mainFiles.slice(i, i + STEP);
        await Promise.all(group.map(async ([origin, alpha, out]) => {
            if (await fs.pathExists(outdir + out)) {
                // console.log(chalk.blue("file exists"), out);
                return;
            }
            await child_process_promise_1.exec(["magick convert", `"${path.resolve(basedir + origin)}"`, `"${path.resolve(basedir + alpha)}"`, "-alpha off -resize 1024x1024 -compose copyopacity -composite", `"${outdir}${out}"`].join(" "));
            console.log(chalk_1.default.green("converted"), out);
        }));
    }
};
