const convertStage = async (cmd = "") => {
    const stages = stage_1.translateStage();
    const stage_preview = _.map(stages, stage_1.translateStagePreview);
    if (cmd === "map") {
        await fs.ensureDir(var_1.TARGET_PREFIX + "maps");
        for (let i = 0; i < stage_preview.length; i++) {
            const [src, dist] = stage_preview[i];
            if ((await fs.pathExists(src)) && !(await fs.pathExists(dist))) {
                console.log(chalk_1.default.green("[map] convert"), `${path.basename(src)} => ${path.basename(dist)}`);
                await child_process_promise_1.exec(`magick convert "${path.resolve(src)}" -resize 512x288! "${dist}"`);
            }
        }
    }
    const luaOutput = util_1.convertObjectToLua(stages, "Stages");
    await fs.outputFile(var_1.TARGET_PREFIX + "StageData.lua", luaOutput);
    await fs.outputFile(var_1.TARGET_PREFIX + "Stage.sync.json", util_1.formatJSON(stages.map(v => {
        return { title: v.name, text: `{{InfoboxStage}}{{NavboxStage}}` };
    })));
};
const convertCharHandbook = async () => {
    const stories = _.map(data_1.handbook_info_table.handbookDict, char => {
        if (!data_1.character_table[char.charID]) {
            if (!char.charID.startsWith("npc"))
                console.log(chalk_1.default.red("[Convert Char Handbook] charid not found"), char.charID);
            return;
        }
        const name = data_1.character_table[char.charID].name;
        const toWikiText = (s) => {
            return s.replace(/\n/g, "<br>").replace(/\. /g, ".&nbsp;");
        };
        return {
            title: `${name}/人员档案`,
            text: char.storyTextAudio
                .map(story => {
                const s = story.stories[0];
                return `{{情报
|storyTitle=${story.storyTitle}
|storyText=${toWikiText(s.storyText)}
|unLockType=${s.unLockType}
|unLockParam=${s.unLockParam}
|unLockString=${s.unLockString}
}}`;
            })
                .join("\n"),
        };
    }).filter(Boolean);
    const chars = _.map(data_1.handbook_info_table.handbookDict, char => {
        if (!data_1.character_table[char.charID])
            return;
        const name = data_1.character_table[char.charID].name;
        return {
            title: `${name}`,
            text: `{{干员页面}}`,
        };
    }).filter(Boolean);
    await fs.writeFile(var_1.TARGET_PREFIX + "Char.sync.json", util_1.formatJSON(chars));
    await fs.writeFile(var_1.TARGET_PREFIX + "CharHandbook.sync.json", util_1.formatJSON(stories));
};
