const convertCVAudio = async () => {
    await fs.ensureDir(var_1.TARGET_PREFIX + "cv");
    const files = _.map(data_1.charword_table, cw => {
        // const char = character_table[cw.charId];
        const id = cw.charId.split("_")[2];
        // if (id != "nightm") return null;
        return [
            var_1.TMP_PREFIX + "DB/AudioClip/assets/torappu/dynamicassets/audio/sound_beta_2/voice/" + cw.voiceAsset.toLowerCase() + ".wav",
            var_1.TARGET_PREFIX + "cv/" + `${id}_${cw.voiceId}.ogg`,
        ];
    }).filter(Boolean);
    // let bats = [];
    await new Promise(resolve => {
        async_1.default.forEachLimit(files, 8, async ([src, dst]) => {
            const cmd = `ffmpeg -i "${src}" -c libvorbis -n -v quiet -ab 128k "${dst}"`;
            if (await fs.pathExists(dst))
                return;
            // console.log(chalk.blue("processing"), cmd);
            // bats.push(cmd);
            try {
                const rst = await child_process_promise_1.exec(cmd);
                if (rst.stderr) {
                    console.log(chalk_1.default.red("[error]"), rst.stderr);
                    throw rst.stderr;
                }
                else {
                    console.log(chalk_1.default.blue("processed"), cmd);
                }
            }
            catch (e) {
                if (e.stderr)
                    console.log(chalk_1.default.red("[error]"), e.stderr);
            }
            return;
        }, err => {
            resolve();
        });
    });
    // await fs.writeFile(TARGET_PREFIX + "cmds.sh", bats.join("\n"));
};
