            dst.canPractice = main.canPractice;
        if (!main.canBattleReplay)
            dst.canBattleReplay = main.canBattleReplay;
        if (main.apCost)
            dst.apCost = main.apCost;
        if (main.apFailReturn)
            dst.apFailReturn = main.apFailReturn;
