Object.defineProperty(exports, "__esModule", { value: true });
exports.translateSkillIcon = (skill) => {
    return [skill.iconId || skill.skillId, skill.levels[0].name];
};
exports.translateSkill = (skill) => {
    const dst = {};
    const base = skill.levels[0];
    dst.name = base.name;
    dst.skillType = base.skillType;
    dst.spType = base.spData.spType;
    if (base.rangeId)
        dst.rangeId = base.rangeId;
    dst.levels = skill.levels.map(lv => {
        const level = {};
        if (lv.duration && lv.duration > 0)
            level.duration = lv.duration;
        if (lv.rangeId !== base.rangeId)
            level.rangeId = lv.rangeId;
        if (lv.spData.spCost)
            level.spCost = lv.spData.spCost;
        if (lv.spData.initSp)
            level.initSp = lv.spData.initSp;
        if (lv.spData.increment != 0 && lv.spData.increment != 1)
            level.increment = lv.spData.increment;
        // if (lv.spData.maxChargeTime != 1 && lv.spData.maxChargeTime != 0) level.maxChargeTime = lv.spData.maxChargeTime;
        const props = lv.blackboard.reduce((r, v) => ((r[v.key] = v.value), r), {});
        level.description = lv.description
            .replace(/\{-?(.+?)(:.+?)?\}/g, (m, key, format) => {
            key = key.toLowerCase();
            const val = (m.startsWith("{-") ? -1 : 1) * props[key];
            if (format == ":0%") {
                return (val * 100).toFixed() + "%";
            }
            if (format == ":0.0%") {
                return (val * 100).toFixed(1) + "%";
            }
            return String(val);
        })
            .replace(/攻击间隔(?:<@ba\.vdown>增大<\/>|<@ba\.vup>.*?缩短<\/>)/g, m => {
            const val = props["base_attack_time"];
            return m + `(${val >= 0 ? "+" : ""}${val})`;
        })
            .replace(/攻击前摇/g, m => {
            const val = props["attack@stun"];
            return m + `(${val >= 0 ? "+" : ""}${val})`;
        });
        return level;
    });
    return dst;
};
